"use client";

import Sidebar from '@/app/component/Sidebar';
import Header from '@/app/component/Header';
import { useState } from 'react';

const sidebarItems = [
  {
    title: 'Dashboard',
    link: '/tms-modules/admin',
    icon: 'üè†',
  },
  {
    title: 'Car Management',
    icon: 'üöò',
    link: '/tms-modules/admin/car-management',
    subItems: [
      { title: 'Manage Cars', link: '/tms-modules/admin/car-management/manage-cars' },
      { title: 'Order Cars', link: '/tms-modules/admin/car-management/order-cars' },
      { title: 'Assign Cars', link: '/tms-modules/admin/car-management/assign-cars' },
      { title: 'Car Attendance', link: '/tms-modules/admin/car-management/car-attendance' },
    ],
  },
  // ... other items
];

export default function AdminLayout({ children }: { children: React.ReactNode }) {
  const [sidebarOpen, setSidebarOpen] = useState(true);

  return (
    <div className="flex h-screen bg-gray-100">
      <Sidebar 
        items={sidebarItems} 
        isOpen={sidebarOpen} 
        onToggle={() => setSidebarOpen(!sidebarOpen)}
      />
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header onToggleSidebar={() => setSidebarOpen(!sidebarOpen)} />
        <main className="flex-1 overflow-y-auto p-6">
          {children}
        </main>
      </div>
    </div>
  );
}